"use client";

import { useEffect, useState } from "react";
import { Sidebar } from "../../components/sidebar";
import { BlockEditor } from "../../components/block-editor";
import { ShareModal } from "../../components/share-modal";

const API_URL = process.env.NEXT_PUBLIC_API_URL ?? "http://localhost:4000/api";

type Page = {
  id: string;
  title: string;
  entries: Array<{ id: string; authorId: string; content: string; createdAt: string }>;
  blockIds: string[];
};

type Block = {
  id: string;
  type: string;
  props: Record<string, unknown>;
  position: number;
};

export default function WorkspacePage() {
  const [token, setToken] = useState<string | null>(null);
  const [currentPage, setCurrentPage] = useState<Page | null>(null);
  const [blocks, setBlocks] = useState<Block[]>([]);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showShareModal, setShowShareModal] = useState(false);
  const [newPageTitle, setNewPageTitle] = useState("");
  const [newPageParentId, setNewPageParentId] = useState<string | null>(null);
  const [sidebarRefresh, setSidebarRefresh] = useState(0);
  const [status, setStatus] = useState<string | null>(null);
  const [editingPageTitle, setEditingPageTitle] = useState(false);
  const [editingPageTitleValue, setEditingPageTitleValue] = useState("");

  useEffect(() => {
    const stored = localStorage.getItem("auth_token");
    if (stored) {
      setToken(stored);
      // URL ÏøºÎ¶¨ ÌååÎùºÎØ∏ÌÑ∞ÏóêÏÑú page ÌôïÏù∏
      const urlParams = new URLSearchParams(window.location.search);
      const pageId = urlParams.get("page");
      if (pageId) {
        loadPage(pageId, stored);
      } else {
        loadPersonalPage(stored);
      }
    } else {
      window.location.href = "/my-space";
    }
  }, []);

  const loadPersonalPage = async (tokenValue: string) => {
    try {
      const res = await fetch(`${API_URL}/pages/me`, {
        headers: { "x-user-token": tokenValue }
      });
      if (res.ok) {
        const data = await res.json();
        setCurrentPage(data);
        setEditingPageTitleValue(data.title);
        setEditingPageTitle(false);
        loadBlocks(data.id, tokenValue);
      } else {
        const status = res.status;
        const errorText = await res.text().catch(() => "Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò");
        console.error("Failed to load personal page:", status, errorText);
        
        // 401 Unauthorized ÎòêÎäî 403 ForbiddenÏù∏ Í≤ΩÏö∞ÏóêÎßå Î¶¨Îã§Ïù¥Î†âÌä∏
        if (status === 401 || status === 403) {
          setStatus("Ïù∏Ï¶ùÏù¥ ÎßåÎ£åÎêòÏóàÏäµÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏Ìï¥Ï£ºÏÑ∏Ïöî.");
          setTimeout(() => {
            localStorage.removeItem("auth_token");
            window.location.href = "/my-space";
          }, 2000);
        } else if (status === 404) {
          // Í∞úÏù∏ ÌéòÏù¥ÏßÄÍ∞Ä ÏóÜÎäî Í≤ΩÏö∞ - ÏÑúÎ≤ÑÏóêÏÑú ÏûêÎèô ÏÉùÏÑ±ÌïòÎØÄÎ°ú Ïû¨ÏãúÎèÑ
          setStatus("Í∞úÏù∏ ÌéòÏù¥ÏßÄÎ•º ÏÉùÏÑ±ÌïòÎäî Ï§ë...");
          // Ïû†Ïãú ÌõÑ Ïû¨ÏãúÎèÑ
          setTimeout(() => {
            loadPersonalPage(tokenValue);
          }, 1000);
        } else {
          // Í∏∞ÌÉÄ ÏóêÎü¨Îäî ÏóêÎü¨ Î©îÏãúÏßÄÎßå ÌëúÏãúÌïòÍ≥† Î¶¨Îã§Ïù¥Î†âÌä∏ÌïòÏßÄ ÏïäÏùå
          setStatus(`ÌéòÏù¥ÏßÄÎ•º Î∂àÎü¨Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§. (Ïò§Î•ò ÏΩîÎìú: ${status})`);
        }
      }
    } catch (error) {
      console.error("Failed to load page", error);
      // ÎÑ§Ìä∏ÏõåÌÅ¨ ÏóêÎü¨Ïù∏ Í≤ΩÏö∞ API ÏÑúÎ≤ÑÍ∞Ä Ïã§Ìñâ Ï§ëÏù∏ÏßÄ ÌôïÏù∏
      setStatus("ÏÑúÎ≤ÑÏóê Ïó∞Í≤∞Ìï† Ïàò ÏóÜÏäµÎãàÎã§. API ÏÑúÎ≤ÑÍ∞Ä Ïã§Ìñâ Ï§ëÏù∏ÏßÄ ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.");
      // ÎÑ§Ìä∏ÏõåÌÅ¨ ÏóêÎü¨Îäî Î¶¨Îã§Ïù¥Î†âÌä∏ÌïòÏßÄ ÏïäÏùå (ÏÇ¨Ïö©ÏûêÍ∞Ä ÏÉàÎ°úÍ≥†Ïπ®Ìï† Ïàò ÏûàÎèÑÎ°ù)
    }
  };

  const loadPage = async (pageId: string, tokenValue?: string) => {
    const tokenToUse = tokenValue || token;
    if (!tokenToUse) return;
    try {
      const res = await fetch(`${API_URL}/pages/${pageId}`, {
        headers: { "x-user-token": tokenToUse }
      });
      if (res.ok) {
        const data = await res.json();
        setCurrentPage(data);
        setEditingPageTitleValue(data.title);
        setEditingPageTitle(false);
        loadBlocks(pageId, tokenToUse);
      } else {
        const status = res.status;
        const errorText = await res.text().catch(() => "Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò");
        console.error("Failed to load page:", status, errorText);
        
        // 401 Unauthorized ÎòêÎäî 403 ForbiddenÏù∏ Í≤ΩÏö∞
        if (status === 401 || status === 403) {
          setStatus("Ïù∏Ï¶ùÏù¥ ÎßåÎ£åÎêòÏóàÏäµÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏Ìï¥Ï£ºÏÑ∏Ïöî.");
          setTimeout(() => {
            localStorage.removeItem("auth_token");
            window.location.href = "/my-space";
          }, 2000);
        } else if (status === 404) {
          // ÌéòÏù¥ÏßÄÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏúºÎ©¥ Í∞úÏù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
          setStatus("ÌéòÏù¥ÏßÄÎ•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§. Í∞úÏù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÌï©ÎãàÎã§.");
          loadPersonalPage(tokenToUse);
        } else {
          // Í∏∞ÌÉÄ ÏóêÎü¨Îäî Í∞úÏù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
          setStatus("ÌéòÏù¥ÏßÄÎ•º Î∂àÎü¨Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§. Í∞úÏù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÌï©ÎãàÎã§.");
          loadPersonalPage(tokenToUse);
        }
      }
    } catch (error) {
      console.error("Failed to load page", error);
      // ÎÑ§Ìä∏ÏõåÌÅ¨ ÏóêÎü¨Ïù∏ Í≤ΩÏö∞ Í∞úÏù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô ÏãúÎèÑ
      setStatus("ÏÑúÎ≤ÑÏóê Ïó∞Í≤∞Ìï† Ïàò ÏóÜÏäµÎãàÎã§. Í∞úÏù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÌï©ÎãàÎã§.");
      if (tokenToUse) {
        loadPersonalPage(tokenToUse);
      }
    }
  };

  const loadBlocks = async (pageId: string, tokenValue: string) => {
    try {
      const res = await fetch(`${API_URL}/pages/${pageId}/blocks`, {
        headers: { "x-user-token": tokenValue }
      });
      if (res.ok) {
        const data = await res.json();
        setBlocks(data || []);
      }
    } catch (error) {
      console.error("Failed to load blocks", error);
      setBlocks([]);
    }
  };

  const handlePageSelect = (pageId: string) => {
    if (token) {
      loadPage(pageId, token);
    }
  };

  const handleCreatePage = (parentId?: string | null) => {
    // parentIdÍ∞Ä nullÏù¥Î©¥ ÏÉÅÏúÑ ÌéòÏù¥ÏßÄ ÏÉùÏÑ± (Í∞úÏù∏ ÏõåÌÅ¨Ïä§ÌéòÏù¥Ïä§ÏôÄ Í∞ôÏùÄ Î†àÎ≤®)
    // parentIdÍ∞Ä ÏûàÏúºÎ©¥ ÌïòÏúÑ ÌéòÏù¥ÏßÄ ÏÉùÏÑ±
    setNewPageParentId(parentId ?? null);
    setShowCreateModal(true);
  };

  const createPage = async () => {
    if (!token || !newPageTitle.trim()) {
      setStatus("ÌéòÏù¥ÏßÄ Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }
    try {
      const payload: any = { title: newPageTitle };
      if (newPageParentId) {
        payload.parentPageId = newPageParentId;
      }
      
      const res = await fetch(`${API_URL}/pages`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "x-user-token": token
        },
        body: JSON.stringify(payload)
      });
      if (res.ok) {
        const data = await res.json();
        setShowCreateModal(false);
        setNewPageTitle("");
        setNewPageParentId(null);
        setStatus("ÌéòÏù¥ÏßÄÍ∞Ä ÏÉùÏÑ±ÎêòÏóàÏäµÎãàÎã§.");
        setSidebarRefresh((prev) => prev + 1);
        loadPage(data.id);
        setTimeout(() => setStatus(null), 2000);
      } else {
        const error = await res.json().catch(() => ({ message: "ÌéòÏù¥ÏßÄ ÏÉùÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§." }));
        setStatus(error.message ?? "ÌéòÏù¥ÏßÄ ÏÉùÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
      }
    } catch (error) {
      console.error("Failed to create page", error);
      setStatus("ÌéòÏù¥ÏßÄ ÏÉùÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
    }
  };

  const handleFileUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
    if (!token || !currentPage || !e.target.files?.[0]) return;

    const file = e.target.files[0];
    setStatus("ÌååÏùº ÏóÖÎ°úÎìú Ï§ë...");
    
    const formData = new FormData();
    formData.append("file", file);
    formData.append("pageId", currentPage.id);

    try {
      const res = await fetch(`${API_URL}/files/upload`, {
        method: "POST",
        headers: {
          "x-user-token": token
        },
        body: formData
      });

      if (res.ok) {
        setStatus(`${file.name} ÌååÏùºÏù¥ ÏóÖÎ°úÎìúÎêòÏóàÏäµÎãàÎã§.`);
        loadBlocks(currentPage.id, token);
        setTimeout(() => setStatus(null), 3000);
      } else {
        const errorText = await res.text();
        console.error("File upload error:", errorText);
        try {
          const errorJson = JSON.parse(errorText);
          setStatus("ÌååÏùº ÏóÖÎ°úÎìúÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§: " + (errorJson.message || errorText));
        } catch {
          setStatus("ÌååÏùº ÏóÖÎ°úÎìúÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§: " + errorText);
        }
      }
    } catch (error) {
      console.error("Failed to upload file", error);
      setStatus("ÌååÏùº ÏóÖÎ°úÎìúÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
    }
    e.target.value = "";
  };

  const handleBlockUpdate = async (blockId: string, props: Record<string, unknown>) => {
    if (!token) return;
    try {
      const res = await fetch(`${API_URL}/pages/blocks/${blockId}`, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          "x-user-token": token
        },
        body: JSON.stringify(props)
      });
      if (res.ok) {
        // Î∏îÎ°ù ÏóÖÎç∞Ïù¥Ìä∏ ÌõÑ Î∏îÎ°ù Î™©Î°ù Îã§Ïãú Î°úÎìú
        if (currentPage) {
          loadBlocks(currentPage.id, token);
        }
      } else {
        console.error("Failed to update block", await res.text());
      }
    } catch (error) {
      console.error("Failed to update block", error);
    }
  };

  const handleBlockCreate = async (type: string, props: Record<string, unknown>) => {
    if (!token || !currentPage) return;
    try {
      const res = await fetch(`${API_URL}/pages/${currentPage.id}/blocks`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "x-user-token": token
        },
        body: JSON.stringify({ type, props })
      });
      if (res.ok) {
        loadBlocks(currentPage.id, token);
      }
    } catch (error) {
      console.error("Failed to create block", error);
    }
  };

  const handleBlocksReorder = async (blockIds: string[]) => {
    if (!token || !currentPage) return;
    try {
      const res = await fetch(`${API_URL}/pages/${currentPage.id}/blocks/reorder`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "x-user-token": token
        },
        body: JSON.stringify({ blockIds })
      });
      if (res.ok) {
        loadBlocks(currentPage.id, token);
      } else {
        console.error("Failed to reorder blocks", await res.text());
      }
    } catch (error) {
      console.error("Failed to reorder blocks", error);
    }
  };

  const handleBlockDelete = async (blockId: string) => {
    if (!token) return;
    if (!confirm("Ïù¥ Î∏îÎ°ùÏùÑ ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?")) return;
    try {
      const res = await fetch(`${API_URL}/pages/blocks/${blockId}`, {
        method: "DELETE",
        headers: {
          "x-user-token": token
        }
      });
      if (res.ok) {
        if (currentPage) {
          loadBlocks(currentPage.id, token);
        }
        setStatus("Î∏îÎ°ùÏù¥ ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.");
        setTimeout(() => setStatus(null), 2000);
      } else {
        setStatus("Î∏îÎ°ù ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
        setTimeout(() => setStatus(null), 2000);
      }
    } catch (error) {
      console.error("Failed to delete block", error);
      setStatus("Î∏îÎ°ù ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
      setTimeout(() => setStatus(null), 2000);
    }
  };

  const handleUpdatePageTitle = async (pageId: string, newTitle: string) => {
    if (!token || !newTitle.trim()) return;
    try {
      const res = await fetch(`${API_URL}/pages/${pageId}`, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          "x-user-token": token
        },
        body: JSON.stringify({ title: newTitle })
      });
      if (res.ok) {
        setSidebarRefresh((prev) => prev + 1);
        // ÌòÑÏû¨ ÌéòÏù¥ÏßÄÏù∏ Í≤ΩÏö∞ Ï†úÎ™© ÏóÖÎç∞Ïù¥Ìä∏
        if (currentPage && currentPage.id === pageId) {
          setCurrentPage({ ...currentPage, title: newTitle });
          setEditingPageTitleValue(newTitle);
        }
      } else {
        const error = await res.json().catch(() => ({ message: "ÌéòÏù¥ÏßÄ Ï†úÎ™© ÏàòÏ†ïÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§." }));
        setStatus(error.message ?? "ÌéòÏù¥ÏßÄ Ï†úÎ™© ÏàòÏ†ïÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
        setTimeout(() => setStatus(null), 2000);
      }
    } catch (error) {
      console.error("Failed to update page title", error);
      setStatus("ÌéòÏù¥ÏßÄ Ï†úÎ™© ÏàòÏ†ïÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
      setTimeout(() => setStatus(null), 2000);
    }
  };

  const handleDeletePage = async (pageId: string) => {
    if (!token) return;
    try {
      const res = await fetch(`${API_URL}/pages/${pageId}`, {
        method: "DELETE",
        headers: {
          "x-user-token": token
        }
      });
      if (res.ok) {
        setStatus("ÌéòÏù¥ÏßÄÍ∞Ä ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.");
        
        // ÏÇ≠Ï†úÎêú ÌéòÏù¥ÏßÄÍ∞Ä ÌòÑÏû¨ ÌéòÏù¥ÏßÄÏù∏ Í≤ΩÏö∞, Ï≤´ Î≤àÏß∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
        if (currentPage && currentPage.id === pageId) {
          const treeRes = await fetch(`${API_URL}/pages/tree`, {
            headers: { "x-user-token": token }
          });
          if (treeRes.ok) {
            const tree = await treeRes.json();
            if (tree.id) {
              loadPage(tree.id, token);
            } else {
              // Ìä∏Î¶¨Í∞Ä ÏóÜÏúºÎ©¥ Í∞úÏù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
              loadPersonalPage(token);
            }
          } else {
            // Ìä∏Î¶¨ Î°úÎìú Ïã§Ìå® Ïãú Í∞úÏù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
            loadPersonalPage(token);
          }
        }
        
        // ÏÇ¨Ïù¥ÎìúÎ∞î ÏÉàÎ°úÍ≥†Ïπ® (ÏïΩÍ∞ÑÏùò ÏßÄÏó∞ ÌõÑ)
        setTimeout(() => {
          setSidebarRefresh((prev) => prev + 1);
        }, 100);
        
        setTimeout(() => setStatus(null), 2000);
      } else {
        const error = await res.json().catch(() => ({ message: "ÌéòÏù¥ÏßÄ ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§." }));
        setStatus(error.message ?? "ÌéòÏù¥ÏßÄ ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
        setTimeout(() => setStatus(null), 2000);
      }
    } catch (error) {
      console.error("Failed to delete page", error);
      setStatus("ÌéòÏù¥ÏßÄ ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
      setTimeout(() => setStatus(null), 2000);
    }
  };

  if (!token) {
    return <div className="workspace-loading">Î°úÎî© Ï§ë...</div>;
  }

  if (!currentPage) {
    return (
      <div className="workspace-loading" style={{ padding: "2rem", textAlign: "center" }}>
        <div>Î°úÎî© Ï§ë...</div>
        {status && (
          <div style={{ marginTop: "1rem", color: "#666", fontSize: "0.9rem" }}>
            {status}
          </div>
        )}
      </div>
    );
  }

  return (
    <div className="workspace-layout">
      <Sidebar
        token={token}
        currentPageId={currentPage.id}
        onPageSelect={handlePageSelect}
        onCreatePage={() => handleCreatePage(null)}
        onCreateSubPage={(parentId) => handleCreatePage(parentId)}
        onDeletePage={handleDeletePage}
        onUpdatePageTitle={handleUpdatePageTitle}
        refreshTrigger={sidebarRefresh}
      />
      <main className="workspace-main">
        <header className="page-header">
          {editingPageTitle ? (
            <input
              type="text"
              value={editingPageTitleValue}
              onChange={(e) => setEditingPageTitleValue(e.target.value)}
              onKeyDown={(e) => {
                if (e.key === "Enter") {
                  handleUpdatePageTitle(currentPage.id, editingPageTitleValue);
                  setEditingPageTitle(false);
                } else if (e.key === "Escape") {
                  setEditingPageTitleValue(currentPage.title);
                  setEditingPageTitle(false);
                }
              }}
              onBlur={() => {
                handleUpdatePageTitle(currentPage.id, editingPageTitleValue);
                setEditingPageTitle(false);
              }}
              style={{
                fontSize: "1.5rem",
                fontWeight: "600",
                padding: "0.5rem",
                border: "2px solid #0066cc",
                borderRadius: "4px",
                width: "100%",
                maxWidth: "600px"
              }}
              autoFocus
            />
          ) : (
            <h1
              onDoubleClick={() => {
                setEditingPageTitleValue(currentPage.title);
                setEditingPageTitle(true);
              }}
              style={{ cursor: "text" }}
              title="ÎçîÎ∏îÌÅ¥Î¶≠ÌïòÏó¨ Ïù¥Î¶Ñ ÏàòÏ†ï"
            >
              {currentPage.title}
            </h1>
          )}
          <div className="page-actions">
            <label className="file-upload-btn">
              üìé ÌååÏùº ÏóÖÎ°úÎìú
              <input type="file" onChange={handleFileUpload} style={{ display: "none" }} />
            </label>
            <button className="button secondary" onClick={() => setShowShareModal(true)}>
              Í≥µÏú†
            </button>
          </div>
        </header>
        {status && (
          <div className="status-message" style={{ 
            padding: "0.5rem 1rem", 
            margin: "0.5rem 0", 
            background: status.includes("Ïã§Ìå®") ? "#ffebee" : "#e8f5e9", 
            borderRadius: "4px",
            color: status.includes("Ïã§Ìå®") ? "#c62828" : "#2e7d32"
          }}>
            {status}
          </div>
        )}
        <div className="page-content">
          <BlockEditor
            token={token}
            pageId={currentPage.id}
            blocks={blocks}
            onBlockUpdate={handleBlockUpdate}
            onBlockCreate={handleBlockCreate}
            onBlockDelete={handleBlockDelete}
            onBlocksReorder={handleBlocksReorder}
          />
        </div>
      </main>
      {showCreateModal && (
        <div className="modal-overlay" onClick={() => setShowCreateModal(false)}>
          <div className="modal" onClick={(e) => e.stopPropagation()}>
            <h2>ÏÉà ÌéòÏù¥ÏßÄ</h2>
            <input
              type="text"
              value={newPageTitle}
              onChange={(e) => setNewPageTitle(e.target.value)}
              placeholder="ÌéòÏù¥ÏßÄ Ï†úÎ™©"
              autoFocus
              onKeyDown={(e) => {
                if (e.key === "Enter") {
                  createPage();
                }
              }}
            />
            <div className="modal-actions">
              <button onClick={createPage}>ÏÉùÏÑ±</button>
              <button onClick={() => setShowCreateModal(false)}>Ï∑®ÏÜå</button>
            </div>
          </div>
        </div>
      )}
      {showShareModal && currentPage && (
        <ShareModal token={token} pageId={currentPage.id} onClose={() => setShowShareModal(false)} />
      )}
    </div>
  );
}
